<template>
    <transition
        :duration="duration"
        :enter-class="enterClass"
        :enter-to-class="enterToClass"
        :enter-active-class="enterActiveClassName"
        :leave-class="leaveClass"
        :leave-to-class="leaveToClass"
        :leave-active-class="leaveActiveClassName"
        @before-enter="(...args) => this.$emit('before-enter', ...args)"
        @enter="(...args) => this.$emit('enter', ...args)"
        @after-enter="(...args) => this.$emit('after-enter', ...args)"
        @before-leave="(...args) => this.$emit('before-leave', ...args)"
        @leave="(...args) => this.$emit('leave', ...args)"
        @after-leave="(...args) => this.$emit('after-leave', ...args)">
        <slot />
    </transition>
</template>

<script>
import camelCase from 'vue-interface/src/Helpers/Functions/camelCase';

export default {

    props: {

        delay: [String, Number],

        duration: [Object, String, Number],

        enter: String,

        enterClass: String,

        leave: String,

        enterToClass: String,

        enterActiveClass: String,
        
        leaveClass: String,

        leaveToClass: String,

        leaveActiveClass: String,

        x: Boolean,

        y: Boolean,

        big: Boolean,

        up: Boolean,

        down: Boolean,

        left: Boolean,

        right: Boolean,

        animated: {
            type: Boolean,
            default: true
        },

        name: {
            type: [Array, String],
            required: true
        },

        direction: {
            type: String,
            default() {
                return (
                    this.x && 'x' ||
                    this.y && 'y' ||
                    this.up && 'up' ||
                    this.down && 'down' ||
                    this.left && 'left' ||
                    this.right && 'right'
                ) || undefined;
            },
            validate(value) {
                return ['up', 'down', 'left', 'right'].indexOf(value.toLowerCase()) !== -1;
            }
        },

        directionEffects: {
            type: Array,
            default: () => ([
                'bounce',
                'fade',
                'flip',
                'lightspeed',
                'rotate',
                'roll',
                'slide',
                'zoom'
            ])
        },

        special: {
            type: Boolean,
            default() {
                return this.directionEffects.indexOf(this.name.toLowerCase()) === -1;
            }
        }

    },

    computed: {

        enterActiveClassName() {
            return this.activeClass('in', this.enterActiveClass);
        },

        leaveActiveClassName() {
            return this.activeClass('out', this.leaveActiveClass);
        }

    },

    methods: {

        activeClass(key, ...classes) {
            return [
                camelCase([
                    this.name,
                    !this.special && key,
                    this.direction,
                    this.big && 'big'
                ].filter(value => !!value).join(' '))
            ]
                .concat([
                    this.animated && 'animated'
                ])
                .concat(classes)
                .join(' ');
        }        

    },

    updated() {
        if(this.delay && this.$slots.default && this.$slots.default.length) {
            const el = this.$slots.default[0].elm;
            
            if(el.style.animatedDelay !== this.delay) {
                el.style.animationDelay = this.delay;
            }
        }
    }

};
</script>

<style lang="scss">
@import "~animate.css";
</style>